input[type="range" i] {
  margin: 0;
}

.rangeContainer{
  display: flex;
  margin: 20px 0px 60px 0px;
}
.slider {
  position: relative;
  width: 80%;
  max-width: 400px;
}

.slider__track,
.slider__range,
.slider__left-value,
.slider__right-value {
  position: absolute;
}

.slider__track,
.slider__range {
  border-radius: 3px;
  height: 5px;
}

.slider__track {
  background-color: #ced4da;
  width: 100%;
  z-index: 1;
}

.slider__range {
  background-color: #02a78b;
  z-index: 2;
}

.slider__left-value,
.slider__right-value {
  color: #000;
  font-size: 14px;
  font-weight: bold;
  margin-top: 25px;
}

.slider__left-value {
  left: 6px;
}

.slider__right-value {
  right: 6px;
}

/* Removing the default appearance */
.thumb,
.thumb::-webkit-slider-thumb {
  -webkit-appearance: none;
}

.thumb {
  pointer-events: none;
  position: absolute;
  height: 0;
  width: 400px;
  outline: none;
}

.thumb--left {
  z-index: 3;
}

.thumb--right {
  z-index: 4;
}

/* For Chrome browsers */
.thumb::-webkit-slider-thumb {
  background-color: #02a78b;
  border: none;
  border-radius: 50%;
  box-shadow: 0 0 2px 2px #fff;
  cursor: pointer;
  height: 18px;
  width: 18px;
  margin-top: 4px;
  pointer-events: all;
  position: relative;
}

/* For Firefox browsers */
.thumb::-moz-range-thumb {
  background-color: #02a78b;
  border: none;
  border-radius: 50%;
  box-shadow: 0 0 1px 1px #ced4da;
  cursor: pointer;
  height: 18px;
  width: 18px;
  margin-top: 4px;
  pointer-events: all;
  position: relative;
}
