

#flyoutMenu {

  width: 20vw;
  height: 100vh;
  background-color: #FFF;

  position: fixed;
  top: 0;
  left: 0;
  transition: transform .3s
              cubic-bezier(0, .52, 0, 1);
  overflow: scroll;
  z-index: 1000;
  padding: 20px;
  padding-top: 30px;
}

#flyoutMenu.hide {
  transform: translate3d(-100vw, 0, 0);

}

#flyoutMenu.show {
  transform: translate3d(0vw, 0, 0);
  overflow: hidden;
}

#flyoutMenu h2 a {
  color: #333;
  margin-left: 15px;
  text-decoration: none;
  font-size: 16px;
  line-height: 24px;
}

#flyoutMenu h2 a:hover {
  text-decoration: none;
}
.fadeIn{
  background: rgba(38, 38, 38, 0.8);
  z-index: 1000;
	position: absolute;
  height: 100%;
    width: 100%;
    transition: all .4s ease!important;
}



